.card {
    /*min-width: 250px;*/
    /*max-width: 450px;*/
    width: 300px;
    padding: 5px;
    margin: 10px;
    background-color: #508CB3;
    color: #000;
}
.card ul {
    background-color: #508CB3;
}
.card a {
    color: #fff;
}

.card a:hover {
    color: #000;
}

.avatar {
    width: 100px;
    height: 100px;
    border-radius: 20px;
}